:root {
  --primary-black: #1f363d;
  --primary-white: #ffffff;
  --error-color: #b32d34;
  --error-bg: rgba(201, 93, 99, 0.1);
  --success-color: #2f9733;
  --success-bg: rgba(47, 99, 49, 0.1);
  --purple: #5b6b97;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  color: #1f363d;
  color: var(--primary-black);
  font-family: 'Cairo', sans-serif;
}

.main-container-wrapper {
  padding: 15px;
  max-width: 800px;
  margin: 0 auto;
}

input {
  background-color: #f4f4f4;
  border: 2px solid #1f363d;
  border: 2px solid var(--primary-black);
  border-radius: 5px;
}

h1 {
  font-size: 48px;
  font-weight: 600;
}

.text-field {
  display: flex;
  flex-direction: column;
}

.text-field {
  width: 100%;
  margin-right: 10px;
}

form button {
  width: 100%;
  margin-top: 15px;
  padding: 5px;
  font-size: 18px;
  border-radius: 5px;
  background-color: var(--purple);
  border: none;
  color: #fff;
  font-family: 'Cairo', sans-serif;
  cursor: pointer;
  transition: 0.3s;
}

form button:hover {
  background-color: #3c4a71;
}

.text-field input {
  margin-top: 5px;
  padding: 10px;
}

.text-field label {
  font-size: 24px;
}

ul {
  margin-top: 30px;
}

ul li {
  list-style-type: none;
}

li {
  padding: 15px;
  margin-top: 25px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

li button {
  padding: 0;
  text-align: center;
  vertical-align: center;
  background: none;
  font-size: 18px;
  cursor: pointer;
}

li button:first-child {
  margin-right: 15px;
}

.complete {
  border-left: 5px solid var(--success-color);
  background-color: var(--success-bg);
  text-decoration: line-through;
}

.complete button {
  color: var(--success-color);
  border: none;
  border-radius: 50%;
}

.incomplete {
  border-left: 5px solid var(--error-color);
  background-color: var(--error-bg);
}

.incomplete button {
  color: var(--error-color);
  border: none;
  border-radius: 50%;
}

